import { db, firebase } from "./index.js";

const comicRef = db.collection("comicshelf");

const shapeData = doc => {
  const post = {
    id: doc.id,
    ...doc.data()
  };
  if (post.timestamp === null) {
    post.timestamp = new Date(Date.now());
  } else {
    post.timestamp = post.timestamp.toDate();
  }
  return post;
};

const listener = (added, modified, removed) => {
  //追加されたメッセージを取得するためのリスナ
  comicRef
    .orderBy("name", "asc")
    // .startAt(new Date(Date.now()))
    .onSnapshot(querySnapshot => {
      querySnapshot.docChanges().forEach(change => {
        if (change.type == "added") {
          added(shapeData(change.doc));
        } else if (change.type == "modified") {
          modified(shapeData(change.doc));
        } else if (change.type == "removed") {
          removed(change.doc.id);
        }
      });
    });
};

const getDatas = (params, order, added) => {
  comicRef
    .orderBy(params, order)
    .get()
    .then(snapshot => {
      if (snapshot.empty) return;
      snapshot.forEach(doc => {
        added(shapeData(doc));
      });
    });
};

const post = (
  name,
  abst,
  img,
  memo,
  app,
  status,
  ev_draw,
  ev_story,
  ev_situ
) => {
  comicRef
    .add({
      name: name,
      abstraction: abst,
      img: img,
      memo: memo,
      applications: app,
      status: status,
      evaluation_drawing: ev_draw,
      evaluation_story: ev_story,
      evaluation_situation: ev_situ,
      evaluation_sum: ev_draw + ev_story + ev_situ,
      timestamp: firebase.firestore.FieldValue.serverTimestamp() //サーバに到達した時間
    })
    // .then(result => {
    //   console.log(result)
    // })
    .catch(error => {
      console.log(error);
    });
};

export { listener, post, getDatas };
