<template>
  <div class="page-wrapper">
    <header class="header-wrapper">
      <h1 class="title">マンガコレクション</h1>
    </header>
    <div class="main-wrapper">
      <div class="post-wrapper">
        <Post />
      </div>
      <div class="request-wrapper">
        <select v-model="params" @change="request">
          <option value="name" selected>名前順</option>
          <option value="status">読書状況順</option>
          <option value="evaluation_sum">評価順 - 総合</option>
          <option value="evaluation_drawing">評価順 - 絵</option>
          <option value="evaluation_story">評価順 - ストーリー</option>
          <option value="evaluation_situation">評価順 - 設定</option>
          <option value="applications">アプリ順</option>
        </select>
        <select v-model="order" @change="request">
          <option value="asc" selected>昇順</option>
          <option value="desc">降順</option>
        </select>
      </div>
      <div class="card-wrapper">
        <Card
          v-for="comic in comics"
          :key="comic.id"
          :id="comic.id"
          :name="comic.name"
          :img="comic.img"
          :applications="comic.applications"
          :abstraction="comic.abstraction"
          :memo="comic.memo"
          :status="comic.status"
          :ev_draw="comic.evaluation_drawing"
          :ev_story="comic.evaluation_story"
          :ev_situ="comic.evaluation_situation"
          :ev_sum="comic.evaluation_sum"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Card from "../components/Card";
import Post from "../components/Post";
import { listener, getDatas } from "../firebase/api.js";

export default {
  name: "Top",
  components: {
    Card,
    Post
  },
  methods: {
    add: function(data) {
      this.comics.push(data);
    },
    modify: function(data) {
      const idx = this.comics.findIndex(comic => comic.id === data.id);
      this.comics[idx] = data;
    },
    delete: function(id) {
      const idx = this.comics.findIndex(comic => comic.id === id);
      this.messages.splice(idx, 1);
    },
    request: function() {
      this.comics = [];
      getDatas(this.params, this.order, this.add);
    }
  },
  mounted: function() {
    listener(this.add, this.modify, this.delete);
  },
  data: function() {
    return {
      params: "name",
      order: "asc",
      comics: []
    };
  }
};
</script>

